"use client";
import React, { useState } from "react";
import { supabase } from "../../database/supabaseClient";

const USERS = [
  {
    label: "user1",
    id: "f2d40632-3610-4039-ac46-9dda9b672497",
    email: "user1@test.com",
    password: "password1",
  },
  {
    label: "user2",
    id: "e04e38e0-7eed-4281-bc05-89fb6c9b30f1",
    email: "user2@test.com",
    password: "password1",
  },
  {
    label: "user3",
    id: "387899c3-fa8a-4582-b6dc-0833dfc02f40",
    email: "user3@test.com",
    password: "password1",
  },
] as const;

const MEMBER_FIELDS = [
  "full_name",
  "hometown",
  "membership_level",
  "profile_status",
  "title",
  "field_of_expertise",
] as const;

const PRIVATE_FIELDS = ["email", "phone", "address", "profile_progress"] as const;

export default function AccessTests() {
  const [log, setLog] = useState<string[]>([]);
  const append = (msg: string) =>
    setLog((prev) => [...prev, `[${new Date().toLocaleTimeString()}] ${msg}`]);

  const runTest = async (user: (typeof USERS)[number] | null) => {
    const actor = user ? user.label : "public";
    append(`\n=== Testing as ${actor} ===`);
    try {
      // Login or logout depending on test user
      if (user) {
        const { error: loginErr } = await supabase.auth.signInWithPassword({
          email: user.email,
          password: user.password,
        });
        if (loginErr) {
          append(`‚ùå Login failed for ${user.email}: ${loginErr.message}`);
          return;
        }
        append(`‚úÖ Logged in as ${user.email}`);
      } else {
        await supabase.auth.signOut();
        append("üë§ Using public (anon) session");
      }

      // Query both tables
      const { data: memberData, error: memberErr } = await supabase
        .from("user_member")
        .select("*");
      const { data: privateData, error: privateErr } = await supabase
        .from("user_private")
        .select("*");

      console.group(`üìä Data for ${actor}`);
      console.log("üë• user_member:", memberData ?? memberErr);
      console.log("üîê user_private:", privateData ?? privateErr);
      console.groupEnd();

      if (memberErr) append(`üö´ user_member error: ${memberErr.message}`);
      if (privateErr) append(`üö´ user_private error: ${privateErr.message}`);

      const memberRows = memberData ?? [];
      const privateRows = privateData ?? [];

      // Evaluate access for each target user
      USERS.forEach((target, i) => {
        if (i > 0) append("\n"); // spacing between users

        const memberRow = memberRows.find((r: any) => r.id === target.id) ?? {};
        const privateRow = privateRows.find((r: any) => r.id === target.id) ?? {};

        MEMBER_FIELDS.forEach((field) => {
          const value = (memberRow as any)[field];
          if (value !== undefined && value !== null) {
            append(
              `${actor} was allowed access to ${target.label} member-field ${field} (${value})`
            );
          } else {
            append(
              `${actor} was NOT allowed access to ${target.label} member-field ${field} (unknown)`
            );
          }
        });

        PRIVATE_FIELDS.forEach((field) => {
          const value = (privateRow as any)[field];
          if (value !== undefined && value !== null) {
            append(
              `${actor} was allowed access to ${target.label} private-field ${field} (${value})`
            );
          } else {
            append(
              `${actor} was NOT allowed access to ${target.label} private-field ${field} (unknown)`
            );
          }
        });
      });

      await supabase.auth.signOut();
      append(`--- End test for ${actor} --- ‚úÖ`);
    } catch (err: any) {
      append(`‚ùå Unexpected error during ${actor} test: ${err.message}`);
      console.error(err);
      await supabase.auth.signOut();
    }
  };

  return (
    <div
      style={{
        fontFamily: "monospace",
        padding: 20,
        maxWidth: 900,
        margin: "auto",
        color: "white",
      }}
    >
      <h2>üîí Access Control / Split-Table (Members vs Private)</h2>
      <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
        {USERS.map((u) => (
          <button
            key={u.email}
            onClick={() => runTest(u)}
            style={{ padding: "8px 12px" }}
          >
            Test as {u.label}
          </button>
        ))}
        <button
          onClick={() => runTest(null)}
          style={{ padding: "8px 12px" }}
        >
          Test as Public
        </button>
      </div>

      <pre
        style={{
          background: "#000",
          color: "#0f0",
          padding: 15,
          borderRadius: 8,
          marginTop: 20,
          height: 500,
          fontSize: 12,
          overflowY: "auto",
          whiteSpace: "pre-wrap",
        }}
      >
        {log.join("\n")}
      </pre>
    </div>
  );
}
